'use client'

import { useEffect, useState, type ChangeEvent, type FormEvent } from 'react'
import { XMarkIcon } from '@heroicons/react/24/outline'

export type ExpenseData = {
  id?: number
  category: string
  amount: string
  date: string
  description: string
}

const defaultCategories = [
  'Material',
  'Personal',
  'Marketing',
  'Miete',
  'Nebenkosten',
  'Transport',
  'Software',
  'Bürobedarf',
  'Sonstiges'
]

const createDefaultFormData = (): ExpenseData => ({
  category: '',
  amount: '',
  date: new Date().toISOString().split('T')[0],
  description: ''
})

type ExpenseModalProps = {
  isOpen: boolean
  onClose: () => void
  onSave: (data: ExpenseData) => void
  initialData?: ExpenseData | null
}

export default function ExpenseModal({ isOpen, onClose, onSave, initialData }: ExpenseModalProps) {
  const [formData, setFormData] = useState<ExpenseData>(createDefaultFormData())

  const handleChange = (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({ ...prev, [name]: value }))
  }

  useEffect(() => {
    if (isOpen) {
      setFormData(initialData ? { ...initialData } : createDefaultFormData())
    }
  }, [isOpen, initialData])

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault()
    if (formData.category && formData.amount) {
      onSave(formData)
      setFormData(createDefaultFormData())
      onClose()
    }
  }

  if (!isOpen) return null

  const isEditing = Boolean(formData.id)
  const total = Number(formData.amount || '0')

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg shadow-lg max-w-md w-full mx-4">
        <div className="flex items-center justify-between p-6 border-b">
          <h2 className="text-xl font-semibold">{isEditing ? 'Ausgabe bearbeiten' : 'Neue Ausgabe'}</h2>
          <button onClick={onClose} className="text-gray-500 hover:text-gray-700">
            <XMarkIcon className="w-5 h-5" />
          </button>
        </div>

        <form onSubmit={handleSubmit} className="p-6 space-y-4">
          <div>
            <label className="block text-sm font-medium mb-1">Kategorie</label>
            <select
              name="category"
              value={formData.category}
              onChange={handleChange}
              className="w-full px-3 py-2 border rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
              required
            >
              <option value="">-- Wählen --</option>
              {defaultCategories.map(cat => (
                <option key={cat} value={cat}>
                  {cat}
                </option>
              ))}
            </select>
          </div>

          <div>
            <label className="block text-sm font-medium mb-1">Betrag (€)</label>
            <input
              type="number"
              name="amount"
              value={formData.amount}
              onChange={handleChange}
              placeholder="0.00"
              step="0.01"
              className="w-full px-3 py-2 border rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
              required
            />
          </div>

          <div>
            <label className="block text-sm font-medium mb-1">Datum</label>
            <input
              type="date"
              name="date"
              value={formData.date}
              onChange={handleChange}
              className="w-full px-3 py-2 border rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>

          <div>
            <label className="block text-sm font-medium mb-1">Beschreibung</label>
            <textarea
              name="description"
              value={formData.description}
              onChange={handleChange}
              placeholder="Details der Ausgabe..."
              rows={3}
              className="w-full px-3 py-2 border rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>

          <div className="bg-gray-50 p-3 rounded-md">
            <div className="flex justify-between font-semibold text-base">
              <span>Gesamtbetrag:</span>
              <span>€ {total.toFixed(2)}</span>
            </div>
          </div>

          <div className="flex gap-3 pt-4">
            <button
              type="button"
              onClick={onClose}
              className="flex-1 px-4 py-2 border rounded-md text-sm font-medium text-gray-700 hover:bg-gray-50"
            >
              Abbrechen
            </button>
            <button
              type="submit"
              className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-md text-sm font-medium hover:bg-blue-700"
            >
              {isEditing ? 'Speichern' : 'Ausgabe erstellen'}
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}
