import { useEffect, useState } from 'react'
import Sidebar from '../components/Sidebar'
import Header from '../components/Header'
import ExpenseModal, { type ExpenseData } from '../components/ExpenseModal'
import CategoryBadge from '../components/CategoryBadge'
import { PencilIcon, TrashIcon } from '@heroicons/react/24/outline'

type Expense = {
  id: number
  category: string
  amount: number
  date: string
  description?: string
  createdAt: string
}

export default function PaymentsPage() {
  const [expenses, setExpenses] = useState<Expense[]>([])
  const [loading, setLoading] = useState(true)
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [editingExpense, setEditingExpense] = useState<Expense | null>(null)
  const [refreshKey, setRefreshKey] = useState(0)

  useEffect(() => {
    loadExpenses()
  }, [refreshKey])

  const loadExpenses = async () => {
    setLoading(true)
    try {
      const res = await fetch('/api/expenses', { cache: 'no-store' })
      if (res.ok) {
        const data: Expense[] = await res.json()
        setExpenses(data)
      }
    } catch (error) {
      console.error('Error loading expenses:', error)
    } finally {
      setLoading(false)
    }
  }

  const handleSaveExpense = async (data: ExpenseData) => {
    try {
      const method = editingExpense ? 'PUT' : 'POST'
      const payload = editingExpense ? { ...data, id: editingExpense.id } : data
      const response = await fetch('/api/expenses', {
        method,
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload)
      })

      if (response.ok) {
        alert(editingExpense ? 'Ausgabe aktualisiert' : `Ausgabe "${data.category}" erstellt`)
        setIsModalOpen(false)
        setEditingExpense(null)
        setRefreshKey(prev => prev + 1)
      } else {
        const err = await response.json()
        alert(err.error || 'Fehler beim Speichern der Ausgabe')
      }
    } catch (error) {
      console.error('Error saving expense:', error)
      alert('Fehler beim Speichern der Ausgabe')
    }
  }

  const handleEdit = (expense: Expense) => {
    setEditingExpense(expense)
    setIsModalOpen(true)
  }

  const handleDelete = async (id: number) => {
    if (!confirm('Ausgabe wirklich löschen?')) return
    try {
      const res = await fetch(`/api/expenses?id=${id}`, { method: 'DELETE' })
      if (res.ok) {
        alert('Ausgabe gelöscht')
        setRefreshKey(prev => prev + 1)
      } else {
        alert('Fehler beim Löschen der Ausgabe')
      }
    } catch (error) {
      console.error('Error deleting expense:', error)
      alert('Fehler beim Löschen der Ausgabe')
    }
  }

  const closeModal = () => {
    setIsModalOpen(false)
    setEditingExpense(null)
  }

  const modalData: ExpenseData | null = editingExpense
    ? {
        id: editingExpense.id,
        category: editingExpense.category,
        amount: editingExpense.amount.toString(),
        date: editingExpense.date || new Date().toISOString().split('T')[0],
        description: editingExpense.description || ''
      }
    : null

  return (
    <div className="flex min-h-screen bg-gray-50">
      <Sidebar />

      <div className="flex-1 p-8">
        <Header />

        <div className="mt-6">
          <div className="flex items-center justify-between mb-6">
            <h1 className="text-2xl font-bold">Zahlungen</h1>
            <button
              onClick={() => {
                setEditingExpense(null)
                setIsModalOpen(true)
              }}
              className="px-4 py-2 bg-blue-600 text-white rounded-md text-sm font-medium hover:bg-blue-700"
            >
              + Neue Ausgabe
            </button>
          </div>

          {loading ? (
            <div className="text-center py-8 text-gray-500">Lädt...</div>
          ) : expenses.length === 0 ? (
            <div className="bg-white rounded-lg shadow p-8 text-center">
              <p className="text-gray-500 mb-4">Noch keine Zahlungen erfasst.</p>
              <button
                onClick={() => setIsModalOpen(true)}
                className="px-4 py-2 bg-blue-600 text-white rounded-md text-sm font-medium hover:bg-blue-700"
              >
                Erste Ausgabe erstellen
              </button>
            </div>
          ) : (
            <div className="bg-white rounded-lg shadow overflow-hidden">
              <table className="w-full">
                <thead className="bg-gray-50 border-b">
                  <tr>
                    <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">KATEGORIE</th>
                    <th className="px-6 py-3 text-right text-sm font-semibold text-gray-700">BETRAG</th>
                    <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">DATUM</th>
                    <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">BESCHREIBUNG</th>
                    <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">AKTIONEN</th>
                  </tr>
                </thead>
                <tbody>
                  {expenses.map((expense, idx) => (
                    <tr key={expense.id} className={idx % 2 === 0 ? 'bg-white' : 'bg-gray-50'}>
                      <td className="px-6 py-4 text-sm font-medium text-gray-900">
                        <CategoryBadge category={expense.category} />
                      </td>
                      <td className="px-6 py-4 text-sm font-semibold text-right text-gray-900">€ {expense.amount.toFixed(2)}</td>
                      <td className="px-6 py-4 text-sm text-gray-600">{expense.date ? new Date(expense.date).toLocaleDateString('de-DE') : '–'}</td>
                      <td className="px-6 py-4 text-sm text-gray-500">
                        {expense.description || 'Keine Beschreibung'}
                      </td>
                      <td className="px-6 py-4 text-sm space-x-3">
                        <button
                          onClick={() => handleEdit(expense)}
                          className="text-blue-600 hover:text-blue-800 inline-flex items-center gap-1"
                        >
                          <PencilIcon className="w-4 h-4" />
                          Bearbeiten
                        </button>
                        <button
                          onClick={() => handleDelete(expense.id)}
                          className="text-red-600 hover:text-red-800 inline-flex items-center gap-1"
                        >
                          <TrashIcon className="w-4 h-4" />
                          Löschen
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </div>

      <ExpenseModal isOpen={isModalOpen} onClose={closeModal} onSave={handleSaveExpense} initialData={modalData} />
    </div>
  )
}
